body {
  font-family: 'Roboto', sans-serif;
  background: #f6f8fa;
  margin: 0px;
}

#root {
}

.sf_tech {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

/* rock-salt-regular - latin */
@font-face {
  font-family: 'Rock Salt';
  font-style: normal;
  font-weight: 400;
  src: url('./fonts/rock-salt-v6-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('Rock Salt'), local('RockSalt'),
       url('./fonts/rock-salt-v6-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('./fonts/rock-salt-v6-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('./fonts/rock-salt-v6-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('./fonts/rock-salt-v6-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('./fonts/rock-salt-v6-latin-regular.svg#RockSalt') format('svg'); /* Legacy iOS */
}

/* roboto-regular - latin */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: local('Roboto'), local('Roboto-Regular'),
       url('./fonts/roboto-v16-latin-regular.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
       url('./fonts/roboto-v16-latin-regular.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* roboto-500 - latin */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  src: local('Roboto Medium'), local('Roboto-Medium'),
       url('./fonts/roboto-v16-latin-500.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
       url('./fonts/roboto-v16-latin-500.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* roboto-700 - latin */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  src: local('Roboto Bold'), local('Roboto-Bold'),
       url('./fonts/roboto-v16-latin-700.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
       url('./fonts/roboto-v16-latin-700.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
